/*Iphone X*/
@media only screen and (max-width:812px) {
    .list{
        color: #fff;
        width: 230px;
        background-color: rgb(55, 89, 191);
        height: 30px;
        margin: 20px auto;
        border-radius: 5px;
        
    }
    .list p{
        padding: 3px;
        position: relative;
    }
    .list p span{
      position: absolute;
    }
    .faicons{
        margin:  5px;
        font-size: 10px;
    }
    .list p input{
        background-color: transparent;
        border: 0;
        color: #fff;
        font-size: 13px;
        text-align: justify;
        margin-left: -50px;
    }
}

