body{
  background-color: #9266ff;
  
}

.App{
  background-color: #3f3160;
  min-height: 500px;
  width: 400px ;
  margin: 50px auto;
  padding: 30px;
 
}
.label{
  text-align: center;
  color: #fff;
  margin-top: -10px;
}

#to-do-form input{
  background-color: rgb(27, 112, 137);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px ;
  font-size: 18px;
  border-radius: 10px;
  color: #fff;
}

#to-do-form input::placeholder{
  color: rgba(255, 255, 255, 0.5);
}

#to-do-form input:focus{
  outline: none;
}

#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: rgb(209, 216, 10);
  cursor: pointer;
  font-weight: bolder;
  color: #fff;
}

#to-do-form button:focus{
  outline: none;
}

/*Iphone X*/
@media only screen and (max-width:812px) {
  .App{
      width: 70% ;
      text-align: center;
  }
  .label{
    text-align: center;
    color: #fff;
    margin-top: -10px;
    font-size: 1.4rem;
  }
  #to-do-form input{
    background-color: rgb(27, 112, 137);
    border: 0;
    width: 150px;
    height: 30px;
    padding: 0 10px;
    margin: 20px 5px ;
    font-size: 12px;
    border-radius: 10px;
    color: #fff;
    text-align: justify;
  }

  #to-do-form button{
    height: 30px;
    width: 80px;
    border-radius: 5px;
    border: 0;
    background-color: rgb(209, 216, 10);
    cursor: pointer;
    font-weight: bolder;
    border-radius: 10px;
    color: #fff;
  }
}